<template>
  <div>
    <div class="wrapper">
        <div class="header" ref="header">
          <div class="background">
            <img src="~common/image/mine/background.png" />
          </div>
          <div class="img-wrapper" ref="imgWrapper">
            <img :src="logoUrl" />
          </div>
          <div class="store-name">
            {{name}}
          </div>
          <div class="person">
            <label>店员：</label>
            <span>{{realName}}</span>
          </div>
        </div>
        <div class="menu">
          <ul class="list">
            <li class="item">
              <span>基本信息</span>
              <i @click="goToBaseInfo"></i>
            </li>
            <li class="item">
              <span>推荐码</span>
              <i @click="goToQRcode"></i>
            </li>
            <li class="item">
              <span>推荐会员</span>
              <i @click="goToIRecommend"></i>
            </li>
          </ul>
          <div class="reset-pwd">
            <span>修改密码</span>
            <i @click="goToPassword"></i>
          </div>
          <div class="change-account" @click="logout">
            切换账号
          </div>
        </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapActions} from 'vuex'
  import {setLoginPage} from 'common/js/mixin'
  import {getCurrentUser,getCurrentMerchant} from 'api/mine'
  import {ERR_CODE,ORIGIN} from 'api/config'

  export default {
    mixins: [setLoginPage],
    data () {
      return {
        logoUrl:'',
        name:'',
        realName: ''
      }
    },
    created () {
      this._getCurrentUser()
      this._getCurrentMerchant()
      window.onresize = () => {
        this._calcWidth()
      }
    },
    mounted () {
      this._calcWidth()
    },
    computed: {
      currentRoute(){
        return this.$router.currentRoute.path
      }
    },
    methods: {
      goToBaseInfo() {
        this.$router.push({
          path: `${this.currentRoute}/baseinfo`
        })
      },
      goToQRcode() {
        this.$router.push({
          path: `${this.currentRoute}/qrcode`
        })
      },
      goToIRecommend() {
        this.$router.push({
          path: `${this.currentRoute}/irecommend`
        })
      },
      goToPassword() {
        this.$router.push({
          path: `${this.currentRoute}/password`
        })
      },
      logout() {
        this.setAutoLogin(false)
        this.$router.push({
          path: '/login'
        })
      },
      _calcWidth() {
        let clientWidth = window.innerWidth
        // 在320px屏幕宽的时候，修改密码被挡住，需要滚动条，这里适当优化下
        if(clientWidth < 340) {
          this.$refs.header.style.height = '200px'
          this.$refs.imgWrapper.style['margin-top'] = '45px'
        } else {
          this.$refs.header.style.height = '220px'
          this.$refs.imgWrapper.style['margin-top'] = '60px'
        }
      },
      _getCurrentUser(){
        getCurrentUser().then((res) => {
          if(res.errCode === ERR_CODE){
            this.realName = res.data.realName
          }
        })
      },
      _getCurrentMerchant(){
        getCurrentMerchant().then((res) => {
          if(res.errCode === ERR_CODE){
            this.logoUrl = res.data.logoUrl
            this.name = res.data.name
          }
        })
      },
      ...mapActions([
        'setAutoLogin'
      ])
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 50px
    .header
      display flex
      align-items center
      flex-direction column
      width 100%
      height 200px
      color #fff
      background-size contain
      background #d3272b
      position relative
      .background
        position absolute
        height 100%
        width 100%
        z-index -1
        img
          display block
          width 100%
          height 100%
      .img-wrapper
        height 70px
        width 70px
        margin-top 60px
        border-radius 50%
        box-sizing border-box
        border 3px solid #fff
        img
          display block
          width 100%
          height 100%
          border-radius 50%
      .store-name
        margin-top 14px
      .person
        margin-top 14px
    .menu
      .list
        margin-top 10px
        background #fff
        .item
          display flex
          align-items center
          justify-content space-between
          font-size $font-size-medium
          height 52px
          box-sizing border-box
          margin-left 14px
          padding-right 14px
          border-bottom 1px solid #ededed
          i
            display flex
            position relative
            background url("~common/image/mine/right-arrow.png") no-repeat
            width 7px
            height 11px
            background-size contain
            &:before
              position absolute
              left -10px
              right -10px
              top -10px
              bottom -10px
              content ''
          &:last-child
            border-bottom none
      .reset-pwd
        background #fff
        display flex
        align-items center
        justify-content space-between
        font-size $font-size-medium
        height 52px
        box-sizing border-box
        padding-left 14px
        padding-right 14px
        margin-top 15px
        i
          display flex
          position relative
          background url("~common/image/mine/right-arrow.png") no-repeat
          width 7px
          height 11px
          background-size contain
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .change-account
        background #fff
        display flex
        height 52px
        align-items center
        justify-content center
        font-size $font-size-medium
        color #808080
        margin-top 15px

</style>



